// AUTO-GENERATED CLASSES

.alignnone {
  max-width: 100%;
  height: auto;
  margin-left: 0;
  margin-right: 0; }

.aligncenter {
  display: block;
  height: auto;
  margin-left: auto;
  margin-right: auto; }

.alignleft, .alignright {
  height: auto;
  margin-left: 0;
  margin-right: 0; }

.alignleft {
  float: left; }

.alignright {
  float: right; }

.wp-caption-text {
  @include fontmake($family: 'Raleway', $size: 0.8); }

