//*********************************************************
// PAGES (INC SUBMIT)
//*********************************************************

.page__wrap {
  @include flex;
  justify-content: center;
  max-width: 120rem;
  padding: 0 3rem; }

.page,
.core--archive {
  @include flex;
  flex-grow: 0;
  margin-right: 0;

  header {
    margin-top: 6rem;

    h1 {
      color: $red;
      font-family: 'Playfair Display', sans-serif;
      font-size: 7rem;
      font-weight: 900; } }

  .page__content {
    flex-grow: 0; } }


//*********************************************************
// ARCHIVE PAGES
//*********************************************************

.core--archive {
  @include flex;
  justify-content: center;

  .archive__header {
    margin-top: 6rem; }

  .post--core:nth-of-type(2n+1) {
    margin-right: gutter(8); }

  // .post--core:nth-of-type(6),
  // .post--core:nth-of-type(7)
  //   margin-bottom: gutter(8)

  // .post--core:nth-of-type(5),
  // .post--core:nth-of-type(7)
 }  //   margin-bottom: gutter(8)


//*********************************************************
// ABOUT PAGE
//*********************************************************

.page--about {
  // margin-top: 6rem

  .about__section {
    margin-bottom: 4rem;

    h1 {
      color: $red;
      font-family: 'Playfair Display', sans-serif;
      font-size: 7rem;
      font-weight: 900;
      text-align: left; } }

  .about__content,
  .about__advisory,
  .about__contact {
    flex-grow: 0;
    margin-bottom: 6rem; }

  .about__masthead {
    flex-grow: 0;

    .masthead__member {
      align-items: center;
      @include flex;
      margin-bottom: 2rem; }

    .masthead__img {
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      border-radius: 50%;
      display: block;
      height: auto;
      max-width: 100%;
      padding-bottom: 100%;
      position: relative;
      width: 100%; }

    .masthead__meta h1 {
      font-family: "Cardo";
      font-size: 1.8rem;
      font-style: italic;
      font-weight: 400;
      // font: 400 italic 1.2rem/1.5 "Alegreya", serif
      margin-top: 1rem;
      text-align: center; }

    .masthead__bio {
      margin-right: 0; } } }

//*********************************************************
// JOURNAL PAGE
//*********************************************************

.page--journal {
  align-items: flex-start; }

.page--journal .col__8 {
  flex-grow: 0; }

.journal__recent {
  background-origin: content-box;
  transition: filter 0.3s ease-in-out;
  margin-right: 0;
  margin-top: 10rem;

  a {
    align-items: center;
    display: block;
    @include flex; }

  .issue__cover {
    align-items: center;
    @include flex;
    margin-bottom: 0; }

  .issue__cover img {
    max-width: 100%; }

  h1 {
    color: $red;
    font-family: "Playfair Display";
    font-size: 4rem; }

  .issue__teaser {
    margin-bottom: 0;
    margin-right: 0;
    padding: gutter() gutter() gutter() 0;

    p {
      font-size: 2.8rem;
      text-align: left; } }

  &:hover {
    filter: brightness(90%) grayscale(30%); } }


.journal__catalog {
  @include flex;
  margin-top: 6rem; }

.catalog__issue {
  flex-grow: 0;
  margin-bottom: 6rem;
  transition: filter 0.3s ease-in-out;

  figure img {
    max-width: 100%; }

  figcaption {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;

    p {
      color: $orange;
      font: 700 2rem 'Fira Sans';
      // font-family: 'Fira Sans'
      // font-size: 2rem;
      // font-weight: 700;
      text-align: center; } }

  &:nth-of-type(3n) {
    margin-right: 0; }

  &:hover {
    filter: brightness(90%) grayscale(30%);

    figcaption {
      opacity: 1; } } }


//*********************************************************
// ISSUE PAGE
//*********************************************************

.issue__meta {
  align-self: flex-start;
  align-items: center; }

.issue__meta img {
  max-width: 100%; }

.issue__meta a {
  display: block;
  font-family: "Fira Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  // font: 300 1rem/1.5 "Raleway", sans-serif
  padding: 1rem 1rem;
  text-align: center;
  text-transform: uppercase;

  &:first-of-type {
    margin-top: -7px; }

  span {
    color: $base;
    display: block;
    font-size: 1.2rem;
 } }    // font: 300 0.7rem/1.5 "Raleway", sans-serif

.issue__content {
  margin-right: 0; }
