//*********************************************************
// GRID SETTINGS AND CONFIGURATIONS
//*********************************************************

$susy : ( container: 120rem, columns: 12, gutters: 1, box-sizing: border-box, gutter-position: after );

.wrap {
  @include container(); }

@mixin flex {
  display: flex;
  flex-wrap: wrap; }

//*********************************************************
// GRID LOOP
//*********************************************************

@mixin col($span:6,$ctx:12) {
  flex: 1 1 span($span of $ctx);
  margin-right: gutter($ctx);
  margin-bottom: 2.4rem; }

@for $i from 1 through 12 {
  .col__#{$i} {
    @include col($span:$i,$ctx:12); }

  .col__#{$i}--8 {
    @include col($span:$i,$ctx:8); }

  .col__#{$i}--6 {
    @include col($span:$i,$ctx:6); }

  .col__#{$i}--4 {
    @include col($span:$i,$ctx:4); } }

//*********************************************************
// OFFSETS
//*********************************************************

@mixin off($push:2,$ctx:12) {
  @include push($push of $ctx); }

@for $i from 1 through 11 {
  .off__#{$i} {
    @include off($push:$i,$ctx:12); }

  .off__#{$i}--8 {
    @include off($push:$i,$ctx:8); } }

// front page
.front-page-featured article {
  @include span(6 of 12); }
.front-page-latest article {
  @include span(12 of 12); }

// pages
.page article.type-page {
  @include span(12 of 12); }

// posts
.single article.type-post {
  @include span(12 of 12); }

// footer
.issue-rand {
  @include span(4 of 12 last); }
.tagline {
  @include span(8 of 12); }
.newsletter {
  @include span(8 of 12); }
.footer-main nav {
  @include span(8 of 12); }
.footer-quote {
  @include span(8 of 12); }
